.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
        // width: 20%;
        width: 25%;
        border-radius: 10%;
    }

    h2 {
        padding: 2rem 1rem 1rem 1rem;
    }

    h3 {
        font-size: 1rem;
    }
}


@media screen and (max-width: 768px) {
    .song-container {
        img {
            // width: 50%;
            width: 40%;
        }
    }
}